import React from 'react'
import SliderT from '../SliderT'
import './testimonials.css'

const Testimonials = () => {
  return (
    <div className='testimonials' id='textimonialsID'>
        <div className="wrapper-testi">
            <div className="heading-testi">
               <p>What do <span>others say</span> about me</p>

               <div className="blur t-blur1"></div>
               <div className="blur t-blur2"></div>
            </div>
            <div className="slider-testi">
                <SliderT/>
            </div>
        </div>
    </div>
  )
}

export default Testimonials