import React from 'react'
import './intro.css'

import {motion} from 'framer-motion'

import img_introBoy from '../../asset/images/intro-boy.png'
import img_introCrown from '../../asset/images/intro-crown.png'
import img_introGlasses from '../../asset/images/intro-glassesimoji.png'
import img_introThumbup from '../../asset/images/intro-thumbup.png'
import img_introVector1 from '../../asset/images/intro-vector-1.png'
import img_introVector2 from '../../asset/images/intro-vector-2.png'
import img_socialFb from '../../asset/images/social-facebook.png'
import img_socialInstaram from '../../asset/images/social-instagram.png'
import img_socialLinkedin from '../../asset/images/social-linkedin.png'
import img_socialGithub from '../../asset/images/social-github.png'



const Intro = () => {
    const transition = {type: 'spring', duration: 2};
    const mobileView = window.innerWidth <= 576 ? true : false;
  
  return (
    <div className='intro' id='homeID'>
        <div className="intro-wrapper">
            <div className="left-intro">
                <div className="intro-content">
                    <span>Hi, I am</span>
                    <span>Anthony De Silva</span>
                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptate, omnis! Lorem ipsum dolor sit amet consectetur adipisicing elit. Tempore, expedita?</p>
                    <button className='my-btnG intro-buton'>Hire Me</button>
                </div>
                <div className="intro-social">
                    <img src={img_socialFb} alt="" />
                    <img src={img_socialInstaram} alt="" />
                    <img src={img_socialLinkedin} alt="" />
                    <img src={img_socialGithub} alt="" />
                </div>
            </div>


            <div className="right-intro">
                <div className="right-intro-wrapper">
                    <img src={img_introVector2} alt="" />
                    <img src={img_introVector1} alt="" />
                    <img src={img_introBoy} alt="" />
                </div>

                <motion.div
                initial = {{top: '0', left: mobileView ? "80%" : "80%"}}
                whileInView = {{left: mobileView ? "60%" : "70%"}}
                transition = {transition}
                 className="upper">
                    <img src={img_introCrown} alt="" />
                    <span>Web <br /> Developer</span>
                </motion.div>

                <motion.div
                initial = {{bottom: '5%', left: mobileView ? '-40px' : '0'}}
                whileInView = {{left: mobileView ? '5%' : '10%'}}
                transition = {transition}
                 className="lower">
                    <img src={img_introThumbup} alt="" />
                    <span>Web <br /> Developer</span>
                </motion.div>

                <motion.div
                 initial={{left: mobileView ? "-50px" : "-100px"}}
                 whileInView={{left:'-8px'}}
                 transition={{...transition, type: 'tween'}}
                 className="glasses-image">
                    <img src={img_introGlasses} alt="" />
                </motion.div>

                <div className="blur intro-top-blur"></div>
                <div className="blur intro-bottom-blur"></div>
            </div>
        </div>
    </div>
  )
}

export default Intro