import React from 'react'
import './navbar.css'
import {Link} from 'react-scroll'

import logoImg from '../../asset/images/logo.png'

const Navbar = (props) => {
  return (
    <div className='my-navbar'>
        <div className="nav-wrapper">
            <div className="left-nav">
                <div className="logo">
                    <img src={logoImg} width="40px" alt="" />
                </div>
            </div>

            <div className="right-nav">
                <div className="nav-menu">
                    <ul>
                        <li>
                            <Link to='homeID' span="true" smooth={true}>Home</Link>
                        </li>
                        <li>
                            <Link to='serviceID' span="true" smooth={true} >Services</Link>
                        </li>
                        <li>
                            <Link to='experienceID' span="true" smooth={true} >Experience</Link>
                        </li>
                        <li>
                            <Link to='projectsID' span="true" smooth={true} >Projects</Link>
                        </li>
                        <li>
                            <Link to='textimonialsID' span="true" smooth={true} >Testimonials</Link>
                        </li>
                    </ul>
                </div>
                <button className='contact-nav-btn'><Link to='contactID' span="true" smooth={true} >Contact</Link></button>
            </div>
        </div>
    </div>
  )
}

export default Navbar