import React from 'react'
import Card from '../Card'
import './services.css'

import {motion} from 'framer-motion'

import servicesImg1 from '../../asset/images/services-1.png'
import servicesImg2 from '../../asset/images/services-2.png'
import servicesImg3 from '../../asset/images/services-3.png'
const Services = () => {
    const transition = {type: 'spring', duration: 2};
    const mobileView = window.innerWidth <= 576 ? true : false;
  return (
    <div className='services' id='serviceID'>
        <div className="services-wrapper">
            <div className="left-services">
                <span className='large-text'>My Awesome</span>
                <span className='large-text'>Services</span>
                <p>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Consequatur id ea vitae corporis. Animi officiis deleniti laboriosam fugit nemo totam!</p>
                <button className='my-btnG'>Download CV</button>

                <div className="blur services-left-blur"></div>
            </div>


            <div className="right-services">
                <div className="cards-s">
                    <div className="cards-left">
                        <motion.div
                        initial = {{top: '', left: mobileView? '-50%' : '0%'}}
                        whileInView = {{left: mobileView? '0' : '20%'}}
                        transition = {transition}
                        >
                            <Card image={servicesImg1} title="Web Design" text="HTML, CSS, Bootstrap, Sass"/>
                        </motion.div>
                    </div>
                    <div className="cards-right">
                       <motion.div
                       initial = {{top: '', left: '50%'}}
                       whileInView = {{left: mobileView? '0' : '20%'}}
                       transition = {transition}
                       >
                            <Card image={servicesImg2} title="Responsive Design" text="Bootstrap, React Bootstrap"/>
                       </motion.div>

                       <motion.div
                       initial = {{top: '', left: '50%'}}
                       whileInView = {{left: mobileView? '0' : '20%'}}
                       transition = {transition}
                       >
                            <Card image={servicesImg3} title="Web Development" text="React JS, Node JS, Express JS"/>
                       </motion.div>
                        
                    </div>
                </div>
                <div className="blur services-right-blur"></div>
            </div>
        </div>
    </div>
  )
}

export default Services