import React from 'react'
import './experience.css'

const Experience = () => {
  return (
    <div className='experience' id='experienceID'>
        <div className="experience-wrapper">
            <div className="item-ex">
                <span>+04</span>
                <span>years</span>
                <span>Experience</span>
            </div>
            <div className="item-ex">
                <span>+99</span>
                <span>completed</span>
                <span>Projects</span>
            </div>
            <div className="item-ex">
                <span>+70</span>
                <span>happy</span>
                <span>Clients</span>
            </div>
        </div>
    </div>
  )
}

export default Experience