import React from 'react'
import './contact.css'

import img_socialFb from '../../asset/images/social-facebook.png'
import img_socialInstaram from '../../asset/images/social-instagram.png'
import img_socialLinkedin from '../../asset/images/social-linkedin.png'
import img_socialGithub from '../../asset/images/social-github.png'

const Contact = () => {
  return (
    <div className='contact' id='contactID'>
        <div className="contact-wrapper">
            <div className="left-contact">
                <span>Get in Touch</span>
                <span>Contact me</span>
            </div>
            <div className="right-contact">
                <form action="">
                    <input type="text" placeholder='Enter your name' />
                    <input type="email" placeholder='Enter your email' />
                    <textarea name="message" cols="30" rows="3" placeholder='Your message'></textarea>
                    <button>Send</button>
                </form>
            </div>
        </div>

        <div className='wave-bg-c'>
            <div className='blur contact-blur-1'></div>
            <div className='contact-social'>
                <img src={img_socialFb} alt="" />
                <img src={img_socialInstaram} alt="" />
                <img src={img_socialLinkedin} alt="" />
                <img src={img_socialGithub} alt="" />
            </div>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="orange" fillOpacity="1" d="M0,224L60,213.3C120,203,240,181,360,181.3C480,181,600,203,720,218.7C840,235,960,245,1080,229.3C1200,213,1320,171,1380,149.3L1440,128L1440,320L1380,320C1320,320,1200,320,1080,320C960,320,840,320,720,320C600,320,480,320,360,320C240,320,120,320,60,320L0,320Z"></path></svg>
        </div>
    </div>
  )
}

export default Contact